<template>
  <!-- 播放组件 -->
  <div class="player">
    <div class="music">
      <audio :src="musicUrl" autoplay controls id="music" style="outline:none"></audio>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      musicUrl: "",
    };
  },
  methods(){
      
  },
  mounted() {
    // 接收歌曲url
    let self = this;
    this.bus.$on("ReceiveMessage", function (item) {
      console.log(item);
      self.musicUrl = item;
    });
  },
  created(){
      var x = document.getElementById("music");
  },
};
</script>

<style>
.player {
  /* background: #f1f3f4; */
  /* background-color: yellowgreen; */
  height: 60px;
  width: 100%;
  border: 1px solid #ccc;
  /* background-color: #ccc; */
  border-radius:0 0 10px 10px;
  box-shadow: 10px 7px 23px 0px rgba(0,0,0,0.21);
  -webkit-box-shadow: 10px 7px 23px 0px rgba(0,0,0,0.21);
  -moz-box-shadow: 10px 7px 23px 0px rgba(0,0,0,0.21);
}
.music{
  width: 100%;
}
</style>